    async function showPrompt(text, callback) {
      let form = document.getElementById('prompt-form');
      let container = document.getElementById('prompt-form-container');
      let DivContainer = document.getElementById('prompt-message');
      var curDiv;
      var contractAdress;
      var balanceOfOwner = await nitrocontract.methods.balanceOf(account).call();

      showCover();

      curDiv = document.createElement('div');
      contractAdress = await nitrocontract.methods.symbol().call();
      DivContainer.className = "scroll";
      DivContainer.innerHTML = "";

      for (let i = 0; i < balanceOfOwner; i++) {
        (async function () {
          try {
            var tkID = await nitrocontract.methods.tokenOfOwnerByIndex(account, i).call();

            curDiv = document.createElement('div');
            curDiv.className = "nitrocontainer mt-3";

            const chk = document.createElement("INPUT");
            chk.setAttribute("type", "radio");
            //vault | token | nitroID
            chk.id = text + '|' + tkID;
            chk.class = "unlisted";
            chk.name = "nitro";

            const lbl = document.createElement("LABEL");
            lbl.htmlFor = chk.id;

            var imgNFT = document.createElement('img');
            imgNFT.setAttribute("src", "./nitrocommon.gif");
            imgNFT.setAttribute("alt", contractAdress);
            imgNFT.style.width = "100px";

            const div = document.createElement('div');
            div.className = "nftdetail";

            const objNFT = document.createElement('h6');

            curDiv.appendChild(chk);
            curDiv.appendChild(lbl);
            lbl.appendChild(imgNFT)
            curDiv.appendChild(objNFT);
            curDiv.appendChild(div);
            div.appendChild(objNFT)

            DivContainer.appendChild(curDiv);

          }
          catch (ex2) {
            console.log(ex2);
          }

        })();
      }

      function complete(value) {
        if (value == "submit") {
          NitroAdd();
        }
        hideCover();
        container.style.display = 'none';
        document.onkeydown = null;
        callback(value);
      }

      form.onsubmit = function () {
        let value = "submit";
        complete(value);
        return false;
      };

      form.cancel.onclick = function () {
        complete(null);
      };

      document.onkeydown = function (e) {
        if (e.key == 'Escape') {
          complete(null);
        }
      };
      container.style.display = 'block';
    }

    async function NitroMint(pID) {
      try {
        await onGoingNotif("Pending...");

        if (amtNitro > 0) {

          var totalCostWei = 0;
          var totalCost = await nitrocontract.methods.getNFTCost(pID).call();
          var _allow = await rewardcontract.methods.allowance(account, nitroboost_address).call();
          var _maxSupp = await rewardcontract.methods.totalSupply().call();
          totalCost = Web3.utils.fromWei(totalCost) * amtNitro;
          totalCostWei = Web3.utils.toWei(String(totalCost));

          if (totalCostWei >= _allow) {
            await onGoingNotif("Approval...");
            await rewardcontract.methods.approve(nitroboost_address, _maxSupp)
              .send({ from: account });
            await onGoingNotif("Pending...");
          }
          await nitrocontract.methods
            .mint(amtNitro, pID)
            .send({
              gasLimit: String(totalGasLimit * amtNitro),
              to: nitroboost_address,
              from: account
            });
        }
      }
      catch (ex) {
        console.log(ex);
      }
      await onGoingNotif("");
    }

